.widget.craft-widget-recent-questions, .widget.craft-widget-recent-reviews {
    margin-left: -24px;
    margin-right: -24px;

    .pane {
        padding: 0;
        border-radius: 0;
        box-shadow: none;
        border-top: 1px solid $c-border;
    }

    .heading {
        padding: 24px;
        margin-bottom: 0 !important;
    }

    .list {
        margin-bottom: -24px;
    }

    .star-rating {
        display: flex;

        .star {
            flex: 1;
            text-align: center;
            padding: 0 10px;

            svg {
                fill: rgba(black, .1);
            }

            &.active {
                svg {
                    fill: $c-yellow;
                }
            }
        }
    }

    .badge-verified {
        color: $c-blue;

        span {
            font-family: $font-regular;
            font-size: 10px;
            letter-spacing: .05em;
        }
    }
}

.qarr-footer {
    display: flex;
    border-top: 1px solid $c-border;
    transition: opacity .3s ease;
    padding: 0 !important;

    // Buttons
    input[type="button"], input[type="submit"] {
        display: block;
        outline: none;
        border: none;
        padding: 18px 0;
        margin: 0;
        height: 100%;
        background: white;
        cursor: pointer;
        -webkit-appearance: none;
        -moz-appearance: none;
    }

    & > input {
        flex: 1;
        font-family: $font-bold;
        font-size: 10px;
        text-transform: uppercase;
        letter-spacing: .05em;
        line-height: normal;
        cursor: pointer;
        transition: all .3s ease;
        border-radius: 0;

        &:first-child {
            border-right: 1px solid $c-border;
        }

        &:hover {
            background: $c-border;
        }
    }

    .btn-modal.submit {
        margin-right: -5px;
    }
}

// Charts
.widget.widget-reviews-donut, .widget.widget-questions-donut {
    display: flex;
    justify-content: center;
    flex-direction: column;
    align-items: center;

    .widget-pie {
        width: 100%;
    }
}


.qarr .widget, .widget.craft-widget-recent-questions, .widget.craft-widget-recent-reviews {

    .body {
        display: flex;

        @include bp('max-width', $tablet) {
            flex-direction: column;

            .chart-explorer-container {
                width: 100%;
            }

            .widget-pie {
                width: 100%;
                margin: 30px 0;
            }
        }
    }


    // TODO: Consolidate from _common.scss
    .no-submissions {
        display: flex;
        align-items: center;
        justify-content: center;

        svg {
            width: 100px;
            opacity: .2;
        }

        .item-wrapper {
            display: flex;
            flex-direction: column;
            align-items: center;
            padding: 50px !important;
            width: 100%;
            box-sizing: border-box;

            p {
                color: darken($c-border, 5);
            }
        }

        .item-actions {
            margin-top: 30px;
        }
    }

    .list {
        .list-item {
            overflow: hidden;
            position: relative;
            display: flex;
            flex-direction: column;
            transition: all .3s ease;
            border-top: 1px solid $c-border;
            min-height: 43px;
            outline: none;

            &:last-child {
                .item-wrapper {
                    border-bottom-left-radius: 5px;
                    border-bottom-right-radius: 5px;
                }
            }

            &.dragging {
                .item-wrapper {
                    border-right: 1px solid $c-border;
                    border-left: 1px solid $c-border;
                }
            }

            &.approve-item {
                .action-other {
                    display: none;
                }
            }

            &.reject-item {
                .action-approve {
                    opacity: 0;
                }
            }

            &.delete-item {
                .action-approve, .action-other {
                    opacity: 0;
                }
            }

            .item-actions {
                position: absolute;
                top: 0;
                right: 0;
                bottom: 0;
                left: 0;
                width: 100%;
                height: 100%;
                display: flex;
                background-color: rgba($c-border, .4);

                .action-other {
                    margin-left: auto;
                }

                & > div {
                    display: flex;
                    justify-content: center;
                    align-items: center;
                    transition: opacity .3s ease;
                }

                svg {
                    transition: all .3s ease;
                }

                a {
                    display: flex;
                    justify-content: center;
                    align-items: center;
                    font-size: 26px;
                    color: $c-lightGray;

                    @include size(100px, 100%);


                    &.approved {
                        color: $c-green;
                        animation: scaleGreen .3s ease-in-out both;
                    }

                    &.rejected {
                        color: $c-red;
                        animation: scaleRed .3s ease-in-out both;
                    }
                }

                .delete-btn:hover {
                    color: $c-red;
                }

                .reject-btn:hover {
                    color: $c-red;
                }
            }

            .item-wrapper {
                position: relative;
                z-index: 1;
                background-color: white;
                padding: 10px 25px;
            }

            &.last {
                padding-bottom: 10px;
            }

            &.has-hover {
                &:hover {
                    box-shadow: 1px 2px 10px rgba(0, 0, 0, .1);
                    transition: all .3s ease-in-out;
                    z-index: 1;
                }
            }

            &.element-actions {
                display: flex;
                padding: 0;

                button, a {
                    align-self: center;
                    width: 100%;
                    display: inline-block;
                    border-radius: 0;
                }

                span {
                    text-align: center;
                }

                .asset-count {
                    padding-left: 7px;
                }
            }

        }

        .item-list {
            display: flex;
            flex-wrap: wrap;
            justify-content: space-between;
        }

        .item-asset {
            display: flex;
            flex-direction: row;
            border: 1px solid $c-border;
            border-radius: $border-radius;
            margin-bottom: 20px;
            width: 48%;

            transition: border .3s ease;

            &.selected {
                border-color: $c-blue;
            }

            .asset-thumbnail {
                border-right: 1px solid $c-border;
                max-width: 80px;

                img {
                    width: 100%;
                }
            }

            .asset-description {
                position: relative;
                display: flex;
                flex-direction: column;
                width: 100%;

                .download-asset {
                    position: absolute;
                    top: 10px;
                    right: 10px;
                }

                .asset-select {
                    position: absolute;
                    bottom: 10px;
                    right: 10px;
                    transition: all .3s ease;
                    color: $c-lightGray;
                    cursor: pointer;

                    &:hover {
                        color: darken($c-lightGray, 20)
                    }

                    &.active {
                        color: $c-blue;
                    }
                }

                .asset-title {
                    padding-right: 20px;
                }
            }

            .pad {
                padding: 10px;
            }
        }

        .item-title, .item-title a {
            color: $c-gray;
            font-size: 12px;
            font-family: $font-bold;
            text-decoration: none;
            transition: all .3s ease;

            &:hover {
                color: lighten($c-gray, 20);
            }
        }

        .item-meta {
            display: flex;
            margin-bottom: 3px;

            .item-meta-icon {
                margin-right: 5px;
                color: $c-lightGray;
            }

            span {
                font-size: 10px;
            }

            .item-meta-title {
                color: $c-lightGray;
                font-family: $font-regular;
            }

            .item-meta-right, .item-meta-right a {
                margin-left: auto;
                color: $c-lightGray;
            }

            .item-pipe {
                color: $c-lightGray;
                padding: 0 10px;
            }

        }
    }

    .list {
        width: 100%;
        margin-right: -5px;

        .item-feedback {
            display: flex;
            align-items: center;
            flex-wrap: wrap;

            .customer-wrapper {
                margin-left: auto;
            }

            .feedback-text {
                width: 100%;
                margin-top: 5px;
                font-family: $font-italic;
                font-size: 12px;
            }
        }
    }

    // Inward styles
    &.inwards {
        .heading {
            padding: 0 24px;
        }

        .pane {
            padding: 24px 0;
        }
    }

    // No footer styles
    &.no-footer {
        .pane {
            padding-bottom: 0;
        }
    }

    .spinner {
        position: absolute;
        right: 20px;
    }

    // Heading
    .heading {
        display: flex;
        align-items: center;
        margin-bottom: 24px;

        h2 {
            margin: 0;
        }

        a {
            margin-left: auto;
            text-decoration: none;
            color: $c-lightGray;
            transition: all .3s ease;

            &:hover {
                color: darken($c-lightGray, 20);
            }
        }
    }


    // Link
    .link {
        margin-left: auto;
        font-size: 10px;

    }
}


.star-rating {

    .star {
        flex: none;
        padding: 0 7px;
    }

    svg {
        @include size(25px);
    }
}


.svg-pie {
    height: 100px;
    padding: 10px;
    transform:rotate(-90deg);
}


.widget-pie {
    width: 30%;
    display: flex;
    flex-direction: column;
    align-items: center;

    .pie-area {
        margin-top: 20px;

        path {
            cursor: pointer;
        }
    }

    .pie-stats {
        display: flex;
        width: 100%;
        margin-top: 25px;
        justify-content: space-evenly;

        .stat-item {
            position: relative;
            display: flex;
            flex-direction: column;
            align-items: center;
            padding-top: 20px;

            &.stat-pending { .stat-icon { border-color: $c-blue; }}
            &.stat-approved { .stat-icon { border-color: $c-green; }}
            &.stat-rejected { .stat-icon { border-color: $c-red; }}

            &.has-hover {
                &.stat-pending { .stat-icon { background: $c-blue; transform: scale(1.4); }}
                &.stat-approved { .stat-icon { background: $c-green; transform: scale(1.4);  }}
                &.stat-rejected { .stat-icon { background: $c-red; transform: scale(1.4);  }}
            }
        }

        .stat-icon {
            position: absolute;
            top:0;
            width: 5px;
            height: 5px;
            margin: 5px 0;
            border-width: 2px;
            border-style: solid;
            border-radius: 50px;
            transform-origin: center center;
            transition: all .3s ease;
        }

        .stat-label {
            font-family: $font-bold;
            font-size: 12px;
            @include antialias;
        }

        .stat-value {
            font-family: $font-bold;
            font-size: 12px;
            color: $c-lightGray;
            @include antialias;
        }
    }
}

.other-menu {
    .item-wrapper {
        animation: pullBounce 700ms linear both;
    }
}

// Geolocation
.widget-top-country-submissions {


    .pane {
        padding: 24px !important;

        & > .reset-geolocation-stats {
            position: absolute;
            top: 20px;
            right: 20px;
            color: $c-lightGray;
        }
    }

    .continent-list {
        display: flex;
        flex-direction: column;
        padding-right: 0;

        .list-item {
            margin-bottom: 10px;
            outline: none;

            &:last-child {
                margin-bottom: 0;
            }
        }

        .item-title {
            font-family: $font-bold;
            font-size: 12px;
            line-height: normal;
            @include antialias;
        }

        .item-progress {
            display: flex;
            align-items: center;

        }

        .item-progress-bar {
            position: relative;
            margin-right: 10px;
            height: 5px;
            width: 100%;
            background: $c-border;
            border-radius: 3px;


            .item-progress-bar-track {
                position: absolute;
                top: 0;
                left: 0;
                width: 0;
                height: 5px;
                border-radius: 3px;

                &.theme-purple {
                    background: $c-purple;
                }
            }

        }

        .item-progress-count {
            font-family: $font-semiBold;
            color: $c-lightGray;
            font-size: 10px;
        }
    }

    .continent-list {
        .details-title {
            font-family: $font-regular;
            font-size: 24px;
            border-bottom: 1px solid $c-border;
        }
    }

}


.widget-top-country-details {
    margin: 10px;

    .details-header {
        margin-top: 20px;

        h2 {
            font-family: $font-light;
            letter-spacing: .05em;
            margin-bottom: 0;
            @include antialias;
        }

        span {
            font-family: $font-regular;
            font-size: 10px;
            color: $c-lightGray;
        }

        .location-total-count {
            font-family: $font-regular;
            font-size: 24px;
        }
    }

    .details-wrapper {
        li {
            display: flex;
            align-items: center;
            margin: 20px 0;

            span {
                margin-right: 10px;
            }

            .country-details {
                font-family: $font-bold;
                font-size: 12px;
                margin-right: 30px;
            }
        }

        .country-details-count {
            font-family: $font-bold;
            margin-left: auto;
            font-size: 11px;
            line-height: normal;
            color: $c-lightGray;

            &.badge {
                flex-shrink: 1;
                padding: 0 6px;
                border-radius: 8px;
                background: $c-border;
            }
        }
    }

    .details-actions {
        margin-top: 30px;
        margin-bottom: 20px;
    }
}


